@import '~antd/dist/antd.css';

/* <576px */
#root .page {
  min-height: 100vh;
}
img {
  max-width: 100%;
}
.wrap {
  /* height: 100%; */
  margin: 0 5px;
}
#footer {
  padding: 24px 0;
}
#header {
  padding: 0;
  height: auto;
}
#header .ant-col {
  height: 100%;
}
#header .logo {
  margin: 0;
  text-align: center;
}
.wrap .topics-list {
  background: #fff;
}
.wrap .topics-list-row {
  padding-left: 10px;
  padding-right: 10px;
}
.wrap .topics-list-row:hover {
  background-color: #f5f5f5;
  background-clip: padding-box;
}
.wrap .topics-list-main-col {
  position: relative;
  display: flex;
  flex-flow: row nowrap;
  justify-content: left;
  align-items: center;
}
.wrap .topics-list-ratio-reply {
  font-size: 10px;
}
.wrap .topics-list-ratio-visit {
  font-size: 10px;
  color: #b4b4b4;
}
.wrap .topics-list-title {
  color: #000;
  min-width: 70%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.wrap .topics-list-title:hover {
  text-decoration: none;
}
.wrap .topics-list-date {
  text-align: right;
  font-size: 11px;
}
.wrap .topic-tag {
  margin: 0 8px;
}
.wrap .topics-list-ratio {
  position: absolute;
  left: 96px;
  top: 20px;
}
#topic .ant-card {
  margin-top: 10px;
}
#topic .author-tag {
  margin-left: 5px;
}
#topic .ant-comment-actions {
  margin: 0;
  position: absolute;
}
.user_page .ant-card {
  margin-top:13px;
}
.user_page .user-details {
  text-align: center;
}
.author_avatar {
  margin-right:10px;
}
.about_content h3{
  line-height: 40px;
  font-size: 24.5px;
  font-weight: 700;
  margin-top: 30px;
  border-bottom: 1px solid #eee;
}
.about_content > div:first-child h3:nth-of-type(1) {
  margin-top: 0;
}
.about-friend-links img {
  width: 250px;
  height: 60px;
}
.topics-pagination {
  margin-top: 10px;
  text-align: center;
}
.topics-pagination .ant-pagination-item{
  margin-right: 4px;
}
.header_menu .ant-menu-item {
  padding-left: 8px;
  padding-right: 8px;
}
@media all and (min-width: 576px) {
  .wrap {
    margin: 0 10px
  }
  .header_menu .ant-menu-item {
    padding-left: 20px;
    padding-right: 20px;
  }
  .topics-pagination .ant-pagination-item{
    margin-right: 8px;
  }
  
}
@media all and (min-width: 768px) {
  .wrap {
    margin: 0 15px;
  }
  .wrap .topics-list-ratio {
    position: relative;
    left: 0;
    top: 0;
  }
  .wrap .topics-list-ratio-reply {
    
    font-size: 14px;
  }
}
@media all and (min-width: 992px) {
  .wrap {
    margin: 0 20px;
  }
  .wrap .topics-list {
    background: #fff;
    padding: 0 15px;
  }
}
@media all and (min-width: 1200px) {
  .wrap {
    margin: 0 auto;
    width: 980px;
  }
  .wrap .topics-list {
    background: #fff;
    padding: 0 20px;
  }
}
@media all and (min-width: 1600px) {
  .wrap {
    margin: 0 auto;
    width: 1200px;
  }
  .wrap .topics-list {
    background: #fff;
    padding: 0 30px;
  }
}